import { Component, Output, EventEmitter } from "@angular/core";
import { FormGroup, FormControl, Validators } from "@angular/forms";
import { filter } from "rxjs/operators";

@Component({
  selector: "side-bar",
  templateUrl: "./sidebar.component.html",
  styles: [
    `
      form {
        margin-top: 62.5px;
        padding-top: 10px;
        padding-bottom: 80px;
        background-color: #f2f2f2;
        height: 100vh;
        width: 300px;
        position: fixed;
        overflow-y: scroll;
      }
      h1 {
        text-align: center;
      }
      a:hover {
        cursor: pointer;
      }
      .form-group {
        padding-left: 10px;
        padding-right: 10px;
      }
      input {
        margin-top: 5px;
      }
      .dropdown {
        padding-left: 10px;
        padding-right: 10px;
      }
      #filter {
        margin-left: 10px;
      }
      #dropdownMenuButton {
        width: 40px;
      }
      #sectorInput {
        padding-right: 50px !important;
      }
    `
  ]
})
export class SidebarComponent {
  @Output() eventClick = new EventEmitter();

  filterStocksForm: FormGroup;
  peMin: FormControl;
  peMax: FormControl;
  pbMin: FormControl;
  pbMax: FormControl;
  marketCapMin: FormControl;
  marketCapMax: FormControl;
  formSector: FormControl;

  ngOnInit() {
    this.peMin = new FormControl("");
    this.peMax = new FormControl("");
    this.pbMin = new FormControl("");
    this.pbMax = new FormControl("");
    this.marketCapMin = new FormControl("");
    this.marketCapMax = new FormControl("");
    this.formSector = new FormControl("");

    this.filterStocksForm = new FormGroup({
      peMin: this.peMin,
      peMax: this.peMax,
      pbMin: this.pbMin,
      pbMax: this.pbMax,
      marketCapMin: this.marketCapMin,
      marketCapMax: this.marketCapMax,
      formSector: this.formSector
    });
  }

  //sends the filter params to the parent component (home component)
  filterStocks(filterValues) {
    this.eventClick.emit(filterValues);
  }
}
